import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vue3GoogleLogin from 'vue3-google-login'
import { google_client_id }  from '../local_property'
import { released_google_client_id }  from '../local_property'


createApp(App)
.use(store)
.use(router)
.use(vue3GoogleLogin, {clientId: released_google_client_id})
.mount('#app')
