class State {
  currentEvents: Event[]
  signInState: boolean
  userMail: string
  userName: string
  isTermAccept: boolean
  myHistoryPrizes: Prize[]
  allHistoryPrizes: Prize[]

  constructor() {
    this.currentEvents = Array<Event>()
    this.signInState = false
    this.userMail = ''
    this.userName = ''
    this.isTermAccept = false
    this.myHistoryPrizes = Array<Prize>()
    this.allHistoryPrizes = Array<Prize>()
  }
}

export default createStore({
  state: new State(),
  getters: {
    getCurrentEvents: state => {
      return state.currentEvents
    },
    // getSignInstate: state => {
    //   return state.signInState
    // }
    getSinInStateEvents: state => {
      return state.signInState
    },
  },
  mutations: {
    setCurrentEvents(state, events: Array<Event>) {
      state.currentEvents = events
    },
    setSignInState(state, isSignIn: boolean) {
      state.signInState = isSignIn
    },
    setUserMail(state, mail: string) {
      state.userMail = mail
    },
    setUserName(state, name: string) {
      state.userName = name
    },
    setTermsAcceptState(state, isTermsAccept: boolean) {
      state.isTermAccept = isTermsAccept
    },
    setMyHistoryPrizes(state, prizes: Array<Prize>) {
      state.myHistoryPrizes = prizes
    },
    setAllHistoryPrizes(state, prizes: Array<Prize>) {
      state.allHistoryPrizes = prizes
    }
  },
  actions: {
  },
  modules: {
    // singInState2: signInStateModule,
    // currentEvents2: currentEventsModule,
  }
})
