import { EventType } from "./Event";

export interface Prize {
    prizeUid: string;
    name?: string;
    description?: string;
    imageUrl?: string;
    eventType?: EventType;
    winDateUTC: number;  // in second
    winnerEmail: string;
}

export function sortByDate(prizeList: Prize[]): Prize[] {
    const sortList =  prizeList.sort((p1 ,p2)=>{
      return p2.winDateUTC - p1.winDateUTC
    })
    return sortList
  }
  